
.registration{
    position: relative;
    // max-width: 480px;
    margin: 0 auto;
    padding-bottom: 20px;
    padding-top: 20px;

    @media screen and (max-width: 767px) {
        &:after {
        display: block;
        content: '';
            
        position: absolute;
        width: 70px;
        height: 70px;
        top: 60px;
        right: 10px;
        z-index: -1;
            
        background-image: url('../images/registration-img/Decor1.svg');
        }
     }

}

.registration-container{
    position: relative;
    width: 100%;
    padding: 40px 8px 32px 8px;
    height: auto;
    margin: 0 auto;
    text-align: center;
    border-radius: 10px;
    box-shadow: 10px 15px 25px rgba(210, 201, 201, 0.45);
    
background-image: linear-gradient(to right, rgba(61,44,44,0.6), rgba(61,44,44,0.6)),
    url("../images/registration-img/topping-on-strawberry-mob1x.png");

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    @media screen and (min-width: 768px) {
    background-image: url("../images/registration-img/Rectangle-tab1x.png"), 
    url("../images/registration-img/topping-on-strawberry-tab1x.png");
    background-position: left, right;
    background-repeat: no-repeat;
    background-size: 570px, 450px;
    height: 246px;
    display: flex;
    align-items: center;
    padding: 0 40px 0 62px;

    &::after {
    display: block;
    content: '';
    position: absolute;
    width: 100px;
    height: 67px;
    bottom: 20px;
    left: 23px;
    background-image: url('../images/registration-img/colorful-and-tasty-tab1x.png');
        }
    }
    
    @media screen and (min-width: 1200px) {
        &::after {
            display: block;
            content: '';
            position: absolute;
            width: 200px;
            height: 133px;
            bottom: 16px;
            left: 15px;
            background-image: url('../images/registration-img/colorful-and-tasty-desk1x.png');
            }
}
}
