.registration__title{
    margin-top: 40px;
    margin-bottom: 27px;
    font-weight: 600;
    font-size: 24px;
    line-height: 1.21;
    text-align: center;
    color: #2E2520;
}

.registration-container__data{
    position: relative;
    display: block;
    margin-bottom: 40px;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.22;
    text-align: center;
    color: $white-color;

    @media screen and (min-width: 768px) {
        color: #000000;
        max-width: 90px;
        margin-bottom: 0;
            }
        }


.registration-container__text{
    margin-bottom: 20px;
    line-height: 1.25;
    color: $white-color;


    @media screen and (min-width: 768px) {
        color: #000000;
        text-align: left;
        margin-bottom: 16px;
        width: 200px;
    }
}

.registration-container__data::after {
    position: absolute;
    width: 100%;
    display: block;
    content: '';
    display: inline-block;
    left: 0px;
    top: 40px;
   
    width: 100%;
    border: 1px solid rgba(255, 255, 255, 1);

    @media screen and (min-width: 768px) {
   
    }
}



.registration-form{
    display: flex;
    flex-direction: column;
    width: 100%;
    // align-items: center;
    text-align: center;
    color: $white-color;

    @media screen and (min-width: 768px) {
    position: relative;
    max-width: 237px;
    margin-left: 56px;
    margin-right: 74px;

    &:after{
        position: absolute;
        width: 154px;
        rotate: 90deg;
        display: block;
        content: '';
        display: inline-block;
        left: -104px;
        top: 42%;
        border: 1px solid rgba(0, 0, 0, 0.5);
    }
    }
    }




.registration-form__input{
    width: 100%;
    margin-bottom: 22px;
    padding-bottom: 2px;
    font-size: 16px;
    line-height: 1.25;
    border-bottom: 1px solid #FFFFFF;
    color: $white-color;
    background-color: transparent;

    @media screen and (min-width: 768px) {
        margin-bottom: 16px;
        line-height: 1, 25;
        color: rgba(0, 0, 0, 0.5);
        border-bottom: 1px solid rgba(0, 0, 0, 1);
        
     }
}

.registration-form__input:last-child {
    margin-bottom: 34px;

    // @media screen and (min-width: 768px) {
    //         margin-bottom: 52px;
    //     }
}


    
.registration-form__button{
    @media screen and (min-width: 768px) {
        height: 40px;
        margin-top: auto;
        margin-bottom: auto;
        }
}
