.speakers-list__text {
  display: block;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  font-weight: 500;
  font-size: 12px;
  line-height: 15px;

  text-align: center;

  @media screen and (min-width: 480px) {
    width: 130px;
  }

  @media screen and (min-width: $breakpoint-tablet) {
    width: 152px;
    font-size: 14px;
  }

  @media screen and (min-width: $breakpoint-decktop) {
    width: 181px;
    font-size: 16px;
  }
}

.speakers-section__title {
  margin-bottom: 12px;
  text-align: center;
  @media screen and (min-width: $breakpoint-tablet) {
    margin-bottom: 20px;
  }

  @media screen and (min-width: $breakpoint-decktop) {
    margin-bottom: 2px;
  }
}

.speakers-list__title {
  margin-bottom: 12px;
  font-size: 16px;
  line-height: 20px;
  text-align: center;

  &::before {
    display: block;
    content: '';

    width: 27px;
    height: 40px;

    position: absolute;

    top: 33px;
    right: 0px;
    background-repeat: no-repeat;

    background-image: url('../images/speakers-images/svg/mobile_left.svg');
  }

  @media screen and (min-width: $breakpoint-tablet) {
    font-weight: $font-w-600;
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 20px;

    &::before {
      display: block;
      content: '';

      width: 27px;
      height: 50px;

      position: absolute;

      top: 21px;
      right: 0px;
      background-repeat: no-repeat;

      background-image: url('../images/speakers-images/svg/tabl-half-right.svg');
    }
  }
  @media screen and (min-width: $breakpoint-decktop) {
    &::before {
      display: block;
      content: '';

      width: 61px;
      height: 100px;

      position: absolute;

      top: 21px;
      right: 0px;
      background-repeat: no-repeat;

      background-image: url('../images/speakers-images/svg/dt_half-right.svg');
    }
  }
}

.speakers-list__image {
  width: 131px;
  height: 141px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 12px;

  @media screen and (min-width: $breakpoint-tablet) {
    margin-bottom: 20px;
    width: 152px;
    height: 160px;
  }
  @media screen and (min-width: $breakpoint-decktop) {
    margin-bottom: 20px;
    width: 288px;
    height: 305px;
  }
}

.section--ring-position {
  padding-top: 18px;
  padding-bottom: 16px;

  position: relative;

  @media screen and (min-width: $breakpoint-tablet) {
    padding-top: 12px;
    padding-bottom: 38px;
  }
  @media screen and (min-width: $breakpoint-decktop) {
    padding-top: 59px;
    padding-bottom: 43px;
  }
}
